:root {
	--container-width: 100vw;
	--first_container_height: 570px;
	--gradient_color_one: violet;
	--gradient_color_two: rgb(218, 22, 218);
	--gradient_color_three: purple;
	--font_color: white;
	--button-width: 15em;
	--button-height: 3.5em;
}

* {
	margin: 0;
	padding: 0;
}

.firstdiv-Container {
	width: 100vw;
	height: var(--first_container_height);
	background-image: linear-gradient(
		45deg,
		var(--gradient_color_one),
		var(--gradient_color_two),
		var(--gradient_color_three)
	);
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.firstdiv-left-elements {
	width: 40vw;
	height: var(--first_container_height);
	overflow: hidden;
}
.firstdiv-right-elements {
	height: var(--first_container_height);
	width: 60vw;
}
.firstdiv-right-elements-header {
	width: 50vw;
	color: var(--font_color);
	font-size: 35px;
	margin-top: 150px;
	margin-left: 20px;
}
.btn {
	width: var(--button-width);
	margin-top: 20px;
	height: var(--button-height);
	border-radius: 50px;
	outline: none;
	border: none;
	font-weight: bold;
	text-transform: uppercase;
	margin-left: 20px;
}

.btn:hover {
	transform: scale(1.05, 1.05);
}

.inner-image {
	height: 375px;
	width: 218px;
	margin-left: 315px;
	margin-top: 192px;
}

.frame {
	position: absolute;
	margin-top: 132px;
	margin-left: 270px;
}
.mobile-element {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;
	position: relative;
	top: 3em;
	left: 13em;
	background-color: rgb(252, 252, 252);
	height: 700px;
	width: 290px;
	border-top-left-radius: 50px;
	border-top-right-radius: 50px;
}
.mobile-image {
	height: 450px;
	width: 270px;
	margin-bottom: 10em;
}
.mobile-speaker {
	height: 7px;
	width: 50px;
	border-radius: 50px;
	background-color: black;
}
