:root {
	--dropdown-head-color: white;
	--dropdown-body-color: rgb(189, 185, 185);
	--dropdown-body-hover-color: rgb(238, 238, 238);
}

.Dropdown {
	height: auto;
}

.dropdown-head {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 78vw;
	height: auto;
	padding: 20px 15px;
	background-color: var(--dropdown-head-color);
	font-weight: bold;
}
.dropdown.inactive {
	width: 78vw;
	height: auto;
	background-color: var(--dropdown-body-color);
	padding: 20px 15px;
	display: none;
}
.btn-dropdown {
	background-color: transparent;
	border: none;
	outline: none;
}
.dropdown-head :hover {
	cursor: pointer;
}
.dropdown.active {
	width: 78vw;
	height: auto;
	background-color: transparent;
	padding: 20px 15px;
	font-size: 15px;
}
.dropdown.active p {
	margin-top: 5px;
}
.dropdown-head:hover {
	background-color: var(--dropdown-body-hover-color);
}
.img-responsive {
	background-color: transparent;
}
