:root{
    --background_color_header: rgb(255, 255, 255);
    --header_height: 7em;
    --width_container: 30vw;
    --height_container: 100vh;
    }
.Top-header{
    height: var(--header_height);
    background-color: var(--background_color_header);
    width: 100vw;
    display: flex;
    justify-content: center;
    border-bottom: rgb(212, 212, 212) solid 1px;

}
.spotifyImage{
    margin: 1.2em 0 ;
    height: 4em;
    width: 13em;
}

.LoginBox{
    width: var(--width_container);
    height: var(--height_container);
    background-color: red;
}