:root{
	--hover-color: rgb(2, 227, 2);
}


.all-list {
	color: white;
}
.all-list li {
	padding: 10px 5px;
}
.all-list :hover{
	color: var(--hover-color);
	cursor: pointer;
}

.footerlinks ul {
	
	list-style-type: none;
	color: white;
	
}
.footerlinks ul li {
	padding: 10px 15px;
	font-size: 11px;
}
.footerlinks ul:hover {
	color: var(--hover-color);
	cursor: pointer;
}
