:root {
	--font-color: white;
	--fifthdiv-height: 500px;
}
* {
	margin: 0;
	padding: 0;
}

.fifthdiv-container {
	height: var(--fifthdiv-height);
	width: 100vw;
	background-image: linear-gradient(
		45deg,
		var(--gradient_color_one),
		var(--gradient_color_two),
		var(--gradient_color_three)
	);
	color: var(--font-color);
	display: flex;
	justify-content: center;
	align-items: center;
}
.fifthdiv-container {
	font-size: 5em;
	text-align: center;
}

.fifthdiv-elements {
	width: 70vw;
	height: 30vh;
	display: flex;
	flex-direction: column;
	justify-content: center;

	align-items: center;
}
.button {
	width: var(--button-width);
	height: var(--button-height);
	border-radius: 50px;
	outline: none;
	border: none;
	font-weight: bold;
	text-transform: uppercase;
	margin-top: 20px;
}
.button:hover {
	transform: scale(1.05, 1.05);
}
