:root {
	--nav_height: 12vh;
	--font_color: white;
	--nav_background_color: black;
	--color_on_hover: rgb(57, 228, 57);
}

* {
	margin: 0;
	padding: 0;
}
.container {
	display: flex;
	justify-content: center;
	background-color: var(--nav_background_color);
}
.left-element img {
	height: 45px;
	width: 150px;
}
.navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 80vw;
	height: var(--nav_height);

	background-color: var(--nav_background_color);
}

.right-element ul {
	list-style: none;
	display: flex;
	color: rgb(255, 255, 255);
	font-weight: bold;
	justify-content: flex-end;
	align-items: center;
}
li {
	position: relative;
	background-color: var(--nav_background_color);
	height: auto;
	padding: 20px;
}

li a:hover {
	cursor: pointer;
	color: var(--color_on_hover);
}
