:root {
	--fourth-backgroud-color: white;
	--hover-color-div4: rgb(226, 225, 225);
}
* {
	margin: 0;
	padding: 0;
}

.fourthdiv-container {
	width: 100vw;
	height: auto;
	padding: 20px 0;
	background-color: var(--fourth-backgroud-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}
.fourthdiv-header {
	text-align: center;
	font-size: 20px;
	height: auto;
	display: flex;
	align-items: center;
}
.fourthdiv-dropdown {
	height: auto;
	width: 80vw;
}
.fourthdiv-dropdown-container {
	display: flex;
	justify-content: center;
	height: auto;
	padding: 5em 0;
}
