:root{
	--width_container: 75vw;
	--footer_background: Black;
}

*{

	padding:0;
	box-sizing:border-box
}

.Footer{
	height: auto;
	width: 100vw;
	background-color: var(--footer_background);
	display: flex;
	justify-content: center;
	
	

}
.Footer-container{
	margin: 70px 0;
	height: auto;
	width: var(--width_container);
	background-color: gray;

}

.Footer-bottom{
	height: auto;
	width: var(--width_container);
	background-color: var(--footer_background);
}
.Footer-last{
	height: auto;
	width: var(--width_container);
	background-color: var(--footer_background);
}
.Footer-divs{
	height: auto;
	width: var(--width_container);
	background-color: var(--footer_background);
	display: flex;
	
}
.Foot-logo{
	width:12vw;
	background-color: var(--footer_background)
}
.Foot-logo img{
	height: 40px;
	width: 140px;
}
.Foot-list{
	width:34vw;
	background-color:var(--footer_background);
}
.Foot-media{
	width:29vw;
    background-color:var(--footer_background);
}

.Foot-list{
	display: flex;
	justify-content: space-between;
}
.lists{
	margin-top:15px;
}
.header{
	margin-top:20px;
	color: gray;
}
.Footer-last{
	display: flex;
	justify-content: space-between;
}
.Footer-last span{
	
	text-align: center;
	display: flex;
	align-items: center;
}
.Footer-bottom span{
	
	margin: 0 10px;

}
.Footer-bottom{
	display: flex;
	justify-content: flex-end;
}
.text-foot{
	margin: 0 10px;
}
.Foot-media{
	display: flex;
	justify-content: flex-end;
}
.circleDiv{
	display: flex;
	justify-content: center;
	align-items: center;
	width:55px;
	height:55px;
	border-radius: 50%;
	background-color: #222326;
	margin: 5px 10px;
}
.svg-footicons{
	transform:scale(1.2);
	color: white;

}
.svg-bottom-footicons{
	transform:scale(0.5);
}
.svg-footicons :hover{
	color: var(--hover-color);
	cursor: pointer;
}
.text-foot, .text-bottom{
	color: gray;
	font-size: 12px;
	display:flex;
	align-items:center;
}
.text-bottom:hover{
	color: var(--hover-color);
	cursor: pointer;
}
.svg-bottom-footicons{
	color: none;
	cursor: default;
}