:root {
	--cards_background: white;
}

* {
	margin: 0;
	padding: 0;
}
.card-mother-container{
	display: flex;
	align-items: center;
	justify-content: center;
	height: 250px;
	background-color: var(--card_background);
	width: 250px;

}
.card-logo-container {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 180px;
	border-radius: 50%;
	background-image: linear-gradient(to bottom,#D32EB2, #7512DD );
	width: 180px;
}


.card-logo{
	display: flex;
	align-items: center;
	justify-content: center;
	height: 90px;
	background-color: rgb(255, 255, 255);
	width: 90px;	
	border-radius: 50%;
}

.svg-icons{
	transform:scale(2.5);
	background-color: transparent;
}

/* D32EB2, 7512DD */


.card-container {
	width: 300px;
	height: auto;
	background-color: var(--cards_background);
	display: flex;
	flex-direction: column;
	align-items: center;
	z-index: 5;
}
.card-title {
	height: auto;
	font-weight: bold;
	font-size: 1.1em;
}
.card-description {
	height: auto;
	margin-top: 10px;
	text-align: center;
	font-size: 1em;
}
