:root {
	--gradient_color_one: violet;
	--gradient_color_two: rgb(218, 22, 218);
	--gradient_color_three: purple;
	--font_color: white;
	--thirddiv-height: 500px;
}

* {
	margin: 0;
	padding: 0;
}

.thirddiv-header {
	width: 100vw;
	height: var(--thirddiv-height);
	background-image: linear-gradient(
		45deg,
		var(--gradient_color_one),
		var(--gradient_color_two),
		var(--gradient_color_three)
	);
	color: var(--font_color);
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;

	font-size: 30px;
	font-style: bold;
}
