:root {
	--second_container_height: 550px;
	--second_container_background_color: rgb(255, 255, 255);
}

* {
	margin: 0;
	padding: 0;
	overflow-x: hidden;
}

.seconddiv-Container {
	width: 100vw;
	height: var(--second_container_height);
	background-color: var(--second_container_background_color);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 5;
}

.seconddiv-elements {
	width: 80vw;
	height: auto;
	background-color: var(--second_container_background_color);
}

.seconddiv-header {
	margin-bottom: 30px ;
	text-align: center;
	height: auto;
	background-color: var(--second_container_background_color);
}
.seconddiv-elements {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
